.sr-only{
	position: absolute !important;
	height: 1px; width: 1px;
	overflow: hidden;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */ clip: rect(1px, 1px, 1px, 1px);
}


.container{
  padding: 1rem 14.5rem 8.8rem 14.5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  max-width: 1920px;
  margin: auto;

  @media screen and (max-width: 76.8rem){
    padding: 1rem 14rem;
  }

  @media screen and (max-width: 76.8rem){
    padding: 1rem;
  }
}

main{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  video {
    width: 100%;
    margin-bottom: 2rem;
  }

  a.link{
    @include link;
    width: auto;
    border-radius: 2.6rem;
    background-color: #6c63ff;
    color: $white;
    padding: 1.6rem 2.5rem;
  }

  table.table{
    width: 100%;
    background-color: #f6f6f6;
    padding: 1rem 3rem;
    margin-bottom: 2rem;

    .entete{
      font-weight: bold;
      color: $primary-color;
    }
    tr{
      margin-bottom: 2rem;

      &:last-of-type{
        :after{
          content: "";
          width: 100%;
          height: 2rem;
          display: block;
        }
      }

    td, th{
      padding: 0;
      color: $grey;
      font-weight: lighter;
      text-align: left;
        &::before{
          content: "";
          width: 100%;
          height: 2rem;
          display: block;
        }
      }
    }

    caption{
      margin-bottom: 2rem;
      color: $primary-color;
    }
  }

  table.presentation{
    img{
      width: 100%;
    }
  }

  section {
    h2, h3{
      @include heading;
    }
  }
  p{
    color: $primary-color;
  }
  #main-content{
    width: 54.11%;
    margin-top: 6rem;
    @media screen and (max-width: 1440px){
      width: 100%;
    }

    ul{
      @include reset-list;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      box-sizing: border-box;
      width: 100%;
      li{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        box-sizing: border-box;
        background: $white;
        padding: 2.9rem 5rem 2.9rem 5rem;
        width: 48.765%;
        border-radius: 2rem;
        margin-bottom: 2.2rem;

        @media screen and (max-width: 768px){
          width: 100%;
        }

        .links-container{
          display: block;
          width: 100%;
          text-align: center;
          a{
            @include link;
            margin: auto;
            &:first-of-type{
              margin-bottom: 1rem;
            }
          }
        }
        h3{
          width: 100%;
          text-align: center;
        }
      }
      &.posts{
        h1, h2, h3{
          @include heading;
        }
        li{
          display: flex;
          width: 100%;
          padding: 4rem;
          .container-image{
            width: 36.1%;
            margin: 0;
            position: relative;
            @media screen and (max-width: 768px){
              width: 100%;
              margin-bottom: 2rem;
            }

            img{
              height: 100%;
              object-fit: contain;
              width: 100%;
              display: block;
            }
            .figcaption{
              position: absolute;
              bottom: 0;
              left: 0;
              margin: 0;
              background: #000000ba;
              color: $white;
              padding: .5rem;
              font-size: 1.4rem;
            }
          }
          .container-content{
            display: flex;
            flex-wrap: wrap;
            width: calc(100% - 36.1%);
            padding-left: 3.6rem;
            @media screen and (max-width: 768px){
              width: 100%;
            }
            h3{
              text-align: left;
              margin-top: 0;
              margin-bottom: 0;
            }
            p{
              font-size: 1.8rem;
              color: $grey5;
              line-height: 2.5rem;
              margin-top: 3.2rem;
              margin-bottom: 3.2rem;
            }
          }
        }
      }
    }
    form{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      background: $white;
      padding: 4rem;
      border-radius: 2rem;
      p{
        width: 100%;
        &.tchat{
          background-color: #ebba4b;
          padding: 2rem 1.5rem;
          border-radius: .5rem;
          color: $white;
          font-weight: bold;
        }
      }

      .container-input{
        width: 45%;
        margin-bottom: 2rem;
        @media screen and (max-width: 768px){
          width: 100%;
        }
        label{
          display: block;
          font-weight: bold;
          color: $primary-color;
          margin-bottom: .5rem;
        }
        input{
          appearance: none;
          border: none;
          padding: 1rem;
          width: 100%;
          border: .1rem solid #eb4e4b;
          border-radius: .5rem;
        }
      }
      button{
        appearance: none;
        border: none;
        @include link;
        border-radius: 2.6rem;
        background-color: #6c63ff;
        color: $white;
        width: auto;
        padding: 1.6rem 2.5rem;
      }
    }
    .white{
      margin-top: 2rem;
      background: #FFF;
      padding: 4rem;
      border-radius: 2rem;
      h2{
        margin-top: 0;
      }
    }
  }
  #ressources{
    position: absolute;
    top: 1rem;
    right: 14.5rem;
    vertical-align: top;
    width: 31.345%;
    max-width: 58.9rem;
    box-sizing: border-box;

    @media screen and (max-width: 1440px){
      position: relative;
      top: unset;
      right: unset;
      width: 100%;
      max-width: none;
    }

    #tools{
      box-sizing: border-box;
      width: 100%;

      h2{
        margin-top: 0rem;
        @media screen and (max-width: 1440px){
          margin-top: 2rem;
        }
      }
      ul{
        @include reset-list;
        li{
          width: 100%;
          margin-bottom: 2.5rem;

          background: $grey3;
          border: .1rem solid $grey4;
          border-radius: 1rem;
          a{
            position: relative;
            display: flex;
            align-items: center;
            width: 100%;
            height: 9rem;
            padding: 2.9rem 1.8rem;
            text-decoration: none;
            .img{
              display: flex;
              justify-content: center;
              align-items: center;
              position: absolute;
              width: 5.1rem;
              height: 5.1rem;
              left: 1.8rem;
              top: 50%;
              transform: translateY(-50%);
              background: $white;
              border-radius: .5rem;
            }
            h3{
              margin-left: 6.9rem;
              width: 100%;
            }
          }

        }
      }
    }

    #usefull_links{
      width: 100%;
      margin-top: 4.4rem;

      ul{
        @include reset-list;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
            width: 45%;
            @media screen and (max-width: 42.5rem){
              width: 100%;
            }
          a{
            position: relative;
            display: flex;
            width: 100%;
            text-decoration: none;
            padding-left: 4.9rem;
            .img{
              position: absolute;
              left: 0;
              top: 50%;
              transform: translateY(-50%);
              display: flex;
              justify-content: center;
              align-items: center;
              width: 3.5rem;
              height: 3.5rem;
              border-radius: .5rem;
              background: $red;
              img{
                width: 2rem;
                height: 2rem;
              }
            }
          }
        }
      }
      &.objectifs-container{
        li{
          width: 100%;
        }
      }
    }
  }

  a{
    &:hover > *,
    &:focus > *{
      text-decoration: underline;
    }
  }
}
