@mixin reset-body{
  margin: 0;
}

@mixin reset-list{
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin heading{
  font-size: 3.8rem;
  color: $primary-color;

  &.n2{
    font-size: 2.6rem;
  }
  &.n3{
    font-size: 1.8rem;
  }
  .lighter{
    font-weight: lighter;
  }
  .bold{
    font-weight: bold;
  }
  .uppercase{
    text-transform: uppercase;
  }
  .br{
    display: block;
  }
  .red{
    color: $red;
  }
  .min{
    margin-top: 1.1rem;
    font-size: 2rem;
  }
  .grey{
    color: $grey;
  }
  &.n3{
    .min{
      font-size: 1.6rem;
    }
    .br{
      margin: 0;
    }
  }
}

@mixin link{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 14.1rem;
  height: 4.5rem;
  background: $greybis;
  border-radius: .5rem;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.8rem;
  color: $primary-color;

  &:hover, &:focus{
    text-decoration: underline;
    background-color: #b2aeff;
    cursor: pointer;
  }
}
