body{
  @include reset-body;
  font-size: 1.8rem;
  max-width: 192rem;
  width: 100%;
  padding: 0;
  margin: auto;
  padding: 1.1rem;
  background: $bg;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  position: relative;

  @media screen and (max-width: 192rem){
    width: 100%;
  }

  *{
    box-sizing: border-box;
  }
}
